// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// ** Invoice List Sidebar
import UiLoader from '@components/ui-loader'
// import AddQuiz from '../add'

// ** Store & Actions
import { getfootballseries, deletefootballseries, viewfootballseries, searchFootballUsers} from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'
import BreadCrumbs from '@components/breadcrumbs'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Filter, Edit, Trash2 } from 'react-feather'
import DataTable from 'react-data-table-component'
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, CustomInput,  Input, Spinner, Badge, Button } from 'reactstrap'
import Swal from 'sweetalert2' 
import withReactContent from 'sweetalert2-react-content'
import { useHistory } from 'react-router-dom'
import Select from 'react-select'
// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { selectThemeColors } from '@utils'
import AppCollapse from '@components/app-collapse'

// ** Table Header
const CustomHeader = ({
  handlePerPage,
  rowsPerPage
}) => {

  
  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Row className='mt-1'>
        <Col xl="6" className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            <Label for="rows-per-page">Show</Label>
            <CustomInput
              className="form-control mx-50"
              type="select"
              id="rows-per-page"
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{
                width: '5rem',
                padding: '0 0.8rem',
                backgroundPosition:
                  'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
              }}
            >
              <option value="10">10</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </CustomInput>
            <Label for="rows-per-page">Entries</Label>
          </div>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.series)
  console.log(store)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [searchName, setSearchName] = useState('')
  const [searchData, setSearchData] = useState([])
  const [searchStatus, setSearchStatus] = useState('')
  const MySwal = withReactContent(Swal)
  const history = useHistory()

  // ** Get data on mount
  const statusCode = searchStatus === 'Live' ? 1 : searchStatus === 'Upcoming' ? 2 : searchStatus === 'Completed' ? 3 : searchStatus === 'Cancelled' ? 4 : ''

  useEffect(() => {
    dispatch(searchFootballUsers(currentPage, searchName, statusCode))
  }, [currentPage])

  useEffect(() => {
    dispatch(getfootballseries(currentPage))
  }, [currentPage])

  const handleSubmit = (currentPage, searchName) => {
    setCurrentPage(1)
    dispatch(searchFootballUsers(currentPage, searchName, statusCode))
  }

  useEffect(() => {
    setSearchData(store.searchFootballUsers?.data)
  }, [handleSubmit])

  useEffect(() => {
    if (store.isloading) {
      <Spinner/>
    } else {
      setListData(store.football_series)
    }
  }, [store.isloading])

//const columnsList = useCallback(columns(), [])
  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }
  const handleConfirmText = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        dispatch(deletefootballseries(id))
        console.log(id)       
      }
    })
  }

  const editHandler = (row) => {
    history.push({pathname:"/footbalseries/edit"})
    dispatch(viewfootballseries(row.id))    
  }

  const statusObj = {
    1: 'light-success',
    2: 'light-warning',
    3: 'light-danger',
    4: 'danger'
  }

  const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
    if (!value) return value 
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))   
  }

  const columns =  [
    {
      name: '#',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) =>  `#${store.football_page.from + pos}` 
    },     
    {
      name: 'series name',
      minWidth: '160px',
      selector: 'series_name',
      sortable: true,
      cell: (row) => (row.series_name)
    },
    {
      name: 'Date&time',
      minWidth: '160px',
      selector: 'date_time',
      sortable: true,
      cell: (row) => formatDate(row.date_time)
      
    },
    {
      name: 'series status',
      minWidth: '160px',
      selector: 'series_status',
      sortable: true,
      cell: (row) => (
        <Badge className="text-capitalize" color={statusObj[row.series_status]} pill>
          {row.series_status === 1 ? "Live" : row.series_status === 2 ? 'Upcoming ' : row.series_status === 3 ? 'Completed' : row.series_status === 4  ? "Cancelled" : ''}
        </Badge>
      ) 
    }, 
    {
      name: 'type',
      minWidth: '160px',
      selector: 'type',
      sortable: true,
      cell: (row) => (row.type === '0' || row.type === '' ? '-' : row.type)
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit" onClick={() => editHandler(row)} />
          {/* </a> */}
          <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.id)} />
        </div>
      )
    }
  ]

  // ** Function in get data on rows per pag

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
   if (searchTerm) {
      const filterList = store.football_series.filter((userItem) => {
        
        const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric',  hour: 'numeric', minute: 'numeric', second: 'numeric'}) => {
          if (!value) return value 
          return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
         
        }

        const formatStatus = (seriesStatus) => { return seriesStatus === 1 ? "Live" : seriesStatus === 2 ? 'Upcoming ' : seriesStatus === 3 ? 'Completed' : seriesStatus === 4  ? "Cancelled" : '' }

        const date = formatDate(userItem.date_time)
        const status = formatStatus(userItem.series_status)

        const newserch =
        userItem.id.toString().toLowerCase().startsWith(searchTerm.toLowerCase()) || 
        userItem.series_name?.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        status?.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        userItem.type.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
        date.toLowerCase().startsWith(searchTerm.toLowerCase()) 
       
       
       const includes = 
       userItem.id.toString().toLowerCase().includes(searchTerm.toLowerCase()) || 
       userItem.series_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
       status?.toLowerCase().includes(searchTerm.toLowerCase()) ||
       userItem.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
       date.toLowerCase().includes(searchTerm.toLowerCase()) 
       
    if (newserch) {
        return newserch
      } else if (!newserch && includes) {
        return includes
      } else return null

      })

      setListData(filterList)
    } else {
      setListData(store.football_series)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination 
  const CustomPagination = () => {
    const count = searchData?.length > 0 ? store.searchFootballUsers?.last_page : store.football_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (searchData?.length >= 0) {
      return searchData
    } else if (listData.length === 0 && isFiltered) {
      return []
    } 
  }

  const handleNameFilter = (value) => {
    setCurrentPage(1)
    setSearchName(value)
  }


  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
    setCurrentPage(1)
    dispatch(searchFootballUsers(currentPage, searchName))
    }
  }

  const statusOptions = [
    { value: '1', label: 'Live' },
    { value: '2', label: 'Upcoming' },
    { value: '3', label: 'Completed' },
    { value: '4', label: 'Cancelled' },
    { value: '5', label: '' }
  ]

  console.log(searchStatus)

  const data = [
    {
      title: <div><Filter size={18}/> Filter...</div>,
      content: (
        <>
        <Row xl={12}>
        <Col
          xl="12"
          className="d-flex align-items-sm-center justify-content  flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="align-items-center mb-sm-0 mb-1 mr-1 ml-1">
            <Label className="mb-0" for="search-invoice">
              Series Name:
            </Label>
            <Input
              id="search-invoice"
              className="w-100"
              type="text"
              value={searchName}
              placeholder='Enter series name'
              onChange={(e) => handleNameFilter(e.target.value)}
              onKeyDown={(e) => handleKeyPress(e)}
            />
          </div>
       </Col>
       </Row>
       <Row>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Series Status:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => setSearchStatus(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                placeholder='search series status...'
                isClearable={true}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            </Col>
          </Row>
          <Row className='m-1'>
          <Button  color ='primary' onClick={() => handleSubmit(currentPage, searchName)} >Submit</Button>
        </Row>
       </>
      )
    }
  ]

  return (
    <Fragment>
       <BreadCrumbs breadCrumbTitle='Series' breadCrumbActive='List' />
       <AppCollapse data={data} type='border' accordion className='mb-1'/>
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList
